import { Component, OnInit, NO_ERRORS_SCHEMA } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { ModalController, LoadingController, ToastController } from '@ionic/angular';
import{ HttpTestService } from '../../http-test.service';
import { AboutComponent } from 'src/app/modals/about/about.component';
import { ForgotPasswordComponent } from 'src/app/modals/forgot-password/forgot-password.component';
import { FormGroup, FormControl } from '@angular/forms';
import { HttpResponse } from '@angular/common/http';
import { GooglePlus } from '@ionic-native/google-plus/ngx';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  providers: [HttpTestService],
  styleUrls: ['./login.page.scss'],
})

export class LoginPage implements OnInit {
  logo:object;
  ngForm: FormGroup;
  data: any;
  response: HttpResponse<Object>;
  header1: string;
  header2: string;
  header3: string;
  isNewUser:boolean = false;

  constructor(private googlePlus: GooglePlus, private  router:  Router, private modalCtrl: ModalController, private service:HttpTestService, private loadingController: LoadingController, private toastController: ToastController) { }


  private loading;
  private toast;
  private toastFail;
  private toastUnreachable;
  ionViewWillEnter(){
    window.sessionStorage.clear();
  }

  ngOnInit() {
    
    this.ngForm = new FormGroup({
      email: new FormControl(''),
      password: new FormControl('')
    });
  }

  doGoogleLogin(){
    this.googlePlus.login({
      'scopes': '', // optional, space-separated list of scopes, If not included or empty, defaults to `profile` and `email`.
      'webClientId': '1027725625353-uqf8t99if8rid5qb9tv0oturfaqsdgbv.apps.googleusercontent.com', // optional clientId of your Web application from Credentials settings of your project - On Android, this MUST be included to get an idToken. On iOS, it is not required.
      'offline': true // Optional, but requires the webClientId - if set to true the plugin will also return a serverAuthCode, which can be used to grant offline access to a non-Google server
    })
    .then(user =>{
      //loading.dismiss();
      localStorage.setItem('google_login','true')
      sessionStorage.setItem('google_user', JSON.stringify({
        name: user.displayName,
        email: user.email,
        picture: user.imageUrl
      }))

      // .then(() =>{
        this.router.navigate(["/home"]);
      // }, error =>{
      //   console.log(error);
      // })
      //loading.dismiss();
      //confirm(JSON.stringify(user))
    }
    , err =>{
      console.log(err)
      //loading.dismiss();
    });
  }
  

  async login(){
/*Criação do loading e toast*/ 
    this.loadingController.create ({
      message: 'Verificando a conta...'
    }).then((overlay) =>{
      this.loading = overlay;
      this.loading.present().then(()=>{
        // setTimeout(() => {
        //   this.loading.dismiss();
        //   this.toastUnreachable.present();
        // }, 30000);
      });
    });

    this.toastController.create ({
      message: 'Login realizado com sucesso.',
      duration: 1000,
      color: 'success',
      position: 'top'
    }).then((overlay) =>{
      this.toast = overlay;    
    });

    this.toastController.create ({
      message: 'Ocorreu um erro ao fazer o login, verifique seus dados, ou tente novamente mais tarde.',
      duration: 3000,
      color: 'danger',
      position: 'top'
    }).then((overlay) =>{
      this.toastFail = overlay;    
    });

    this.toastController.create ({
      message: 'Serviço indisponível. Tente novamente mais tarde.',
      duration: 3000,
      color: 'danger',
      position: 'top'
    }).then((overlay) =>{
      this.toastUnreachable = overlay;    
    });


/*Fim da criação do loading e toast*/

    //console.log(form.value);
    //this.router.navigateByUrl('home');
     this.service.login("api/auth/sign_in",this.ngForm).subscribe(data => {
       if(data.ok){
        this.loading.dismiss();
        this.toast.present();
        this.header1 = window.sessionStorage.getItem('uid');
        this.header2 = window.sessionStorage.getItem('access-token');
        this.header3 = window.sessionStorage.getItem('client');
        console.log("ESSE É O BODY"+JSON.stringify(data.body));
        console.log("ESSE É O BODY"+data.body.data.user_type);
        console.log("ESSES SÃO OS HEADERS!"+ this.header1, this.header2, this.header3);
        var permission = data.body.data.user_type;      
        window.sessionStorage.setItem('user_type', (data.body.data.user_type).toString())
        window.sessionStorage.setItem('isNewUser', (data.body.data.isNewUser).toString())
        // let isNewUser: NavigationExtras = {
        //   queryParams: {
        //     isNewUser: data.body.data.isNewUser
        //   }
        // }
        if(window.sessionStorage.getItem('user_type') == '1' || window.sessionStorage.getItem('user_type') == '2' || window.sessionStorage.getItem('user_type') == '3'  ){
          this.router.navigateByUrl('support-list');
        }else if(window.sessionStorage.getItem('user_type') == '0'){
          this.router.navigate(['home']);
        }else{
          this.router.navigate(['login']);
        }
        
       }else {
        
        confirm('Login Invalido');
       }    
       this.ngForm.reset();
     }, error => {
      this.loading.dismiss();
      this.toastFail.message = error.error.errors
      this.toastFail.present();
    });
   
  }

  
  async forgot_password(){
    const modal = await this.modalCtrl.create({
      component: ForgotPasswordComponent,
      cssClass:'login_page_forgot_password_modal'
      
    });
    return await modal.present();

  }
  async about(){
    const modal = await this.modalCtrl.create({
      component: AboutComponent,
      cssClass:'login_page_about_modal'
    });
    return await modal.present();

  }
  
}